import * as React from "react";
import { Dialog, DialogContent } from "@mui/material";
import { ModalDialogprops } from "../../types/types";
import CloseIcon from "@mui/icons-material/Close";
import { usePopupClose } from "../../assets/hooks";

const CustomModal = ({ open, children, additionalData }: ModalDialogprops<{ width: any, height?: any, show?: any, close?: any }>) => {
  const closePopupWithScrollRestore = usePopupClose();

  const handleClose = (event: any, reason?: string) => {
    // Don't close if clicking on elements with data-no-close attribute
    const noCloseElement = event?.target?.closest('[data-no-close]');
    
    if (noCloseElement) {
      return;
    }
    
    if (additionalData?.close === "none") {
      console.log("");
    } else {
      closePopupWithScrollRestore();
    }
  };

  const handleCloseIconClick = (event: React.MouseEvent) => {
    handleClose(event);
  };

  const width = additionalData?.width || { xs: "92vw", md: "760px", lg: "840px" } as any;
  const height = additionalData?.height || { xs: "auto", md: "540px", lg: "560px" } as any;

  return (
    <>
      <Dialog
        open={open}
        onClose={handleClose}
        scroll="paper"
        disableScrollLock={true}
        disableEnforceFocus={true}
        disableAutoFocus={true}
        keepMounted={false}
        container={() => document.body}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        style={{transition:"width 0.5s,height 0.5s"}}
        PaperProps={{
          sx: {
            minWidth: open ? "200px" : "0px", 
            minHeight: open ? "100px" : "0px",
            height: open ? height : "0px",
            width: open ? width : "0px",
            padding: open ? "0" : "0px",
            maxWidth: open ? "1300px" : "0px",
            maxHeight: open ? "88vh" : "0px",
            position:"relative",
            zIndex:1300,
            overflowY: "auto",
            borderRadius: { xs: "12px", md: "12px" },
          },
        }}
      >
        {additionalData?.show === "none" ? "" :
          <CloseIcon
            onClick={handleCloseIconClick}
            sx={{
              position: "absolute",
              right: 14,
              top: 6,
              zIndex: 1,
              color: "grey",
              cursor: "pointer",
              backgroundColor: "#f5f5f5",
              borderRadius: "50%",
              padding: "6px",
              width: "14px",
              height: "14px",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              transition: "all 0.2s ease-in-out",
              border: "1px solid #e0e0e0",
              marginBottom: "8px",
              "&:hover": {
                backgroundColor: "#F48438",
                color: "#fff",
                transform: "scale(1.1)",
                borderColor: "#F48438",
                boxShadow: "0 2px 8px rgba(255, 68, 68, 0.3)"
              }
            }}
          />}
        
        <DialogContent sx={{ padding: 0 }}>
          {children}
        </DialogContent>
      </Dialog>
    </>
  );
};

export default CustomModal;
